<template>
    <Nav />
    <h2>GASTRONOMÍA_vlc</h2>
    <main>
        <Restaurant :restaurants="this.restaurants" />
    </main>
    <Footer />
</template>

<script>
import apiUrl from "../../config.js"
import Nav from '../components/Nav.vue'
import Restaurant from '../components/Restaurant'
import Footer from '../components/Footer.vue'

export default {
    name: "Gastronomy",
    components: {
        Nav,
        Restaurant,
        Footer
    },
    data() {
        return {
            restaurants: []
        }
    },
    methods: {
        async fetchRestaurants() {
            const res = await fetch(`${apiUrl}/api/restaurants`)
            const data = await res.json()
            return data
        }
    },
    async created() {
        this.restaurants = await this.fetchRestaurants()
    }
}
</script>