<template>
    <section v-for="(restaurant, i) in restaurants" :key="i">
        <div class="restaurant-img">
            <img :src="'https://horizontes.herokuapp.com/images/restaurants/' + restaurant.picture" :alt="restaurant.picture">
            <div class="restaurant-type">
                <span>gastronomía</span>
                <img :src="'https://horizontes.herokuapp.com/images/flags/' + restaurant.tag + '.png'" :alt="restaurant.tag">
            </div>
        </div>
        <div class="restaurant">
            <div class="restaurant-header">
                <h3>{{restaurant.name}}</h3>    
                <span v-if="restaurant.socialNetworks">
                    <a v-for="socialNetwork in restaurant.socialNetworks" :key="socialNetwork" :href="socialNetwork" :title="socialNetwork">
                        <i :class="'fab fa-' + socialNetwork.split('//')[1]"></i>
                    </a>
                </span>
            </div>            
            <p>{{restaurant.address}}</p>
            <p><span>reservas: </span><a :href="'tel:' + restaurant.telf">{{restaurant.telf}}</a></p>
            <button v-if="this.showMenu != restaurant._id" @click="toggleMenu(restaurant._id)">+ info</button>
            <button v-if="this.showMenu == restaurant._id" @click="toggleMenu(restaurant._id)">- info</button>
            <div v-if="this.showMenu == restaurant._id" class="menu">
                <h4>NUESTRA CARTA</h4>
                <img :src="'https://horizontes.herokuapp.com/images/restaurants/' + restaurant.qrmenu" :alt="restaurant.qrmenu">
                <p>{{restaurant.menu[0]}}</p>
                <ul>
                    <li>entrante 1</li>
                    <li>entrante 2</li>
                </ul>
                <p>{{restaurant.menu[1]}}</p>
                <ul>
                    <li>bla bla 1</li>
                    <li>bla bla 2</li>
                </ul>
                <p>{{restaurant.menu[2]}}</p>
                    <li>opcion 1</li>
                    <li>opcion 2</li>
            </div>
        </div>     
    </section>
</template>

<script>
export default {
    name: "Restaurant",
    props: {
        restaurants: Array
    },
    data() {
        return {
            showMenu: null    
        }
    },
    methods: {
        toggleMenu(id) {
            if (this.showMenu != id) {
                return this.showMenu = id    
            }
            return this.showMenu = null
        }
    }
}
</script>

<style lang="scss" scoped>
section {
    width: fit-content;
    box-shadow: 0px 0px 14px 0px #00000038;
    line-height: 1.4;  
    margin: 1rem 0 2rem 0; 
}
.restaurant-img {
    border-radius: 8px 8px 0 0;
    overflow: hidden;
    max-width: 600px;
    max-height: 320px;
    position: relative;
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    .restaurant-type {
        color: white;
        background-color: rgba(0, 0, 0, 0.4);
        display: flex;
        align-items: center;
        position: absolute;
        top: 8px;
        right: 8px;
        padding: 0 2px;
        span {
            margin-right: 8px;
        }
        img {
            height: 32px;
            width: 32px;
        }
    }
}
.restaurant {
    padding: 8px 16px;
    .restaurant-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 0.5rem;
        a {
            padding-left: 8px;
        }
    }
    button {
        font-weight: 600;
        font-size: 14px;
        width: 100%;
        text-align: right;
        &:hover {
            color: #0bd3d3;    
        }
        &:active {
        transform: none; 
    }
    }
}
.menu {
    // opening menu animation
    animation: myToggleAnimation 900ms linear;
    @keyframes myToggleAnimation {
        from {opacity: 0.2};
        to {opacity: 1};
    }
    img {
        float: right;
        height: 80px;
    }
    li {
        font-size: 12px;
    }
}
</style>